/**
 * Quickly call WebApis on the current App
 *
 * @export
 * @class SxcApiService
 */
class SxcApiService {
    http;
    controller;
    constructor(http, controller) {
        this.http = http;
        this.controller = controller;
    }
    /**
     * Get the request URL of the api
     */
    url(method) {
        return `${routeApi}/${this.controller}/${method}`;
    }
    /**
     * Do a GET request to the specified 2sxc api controller
     */
    get(method, urlParams) {
        const requestParams = getHttpParams(urlParams);
        return this.http.get(this.url(method), { params: requestParams });
    }
    /**
     * Do a POST request to the specified 2sxc api controller
     */
    post(method, urlParams, body) {
        const requestParams = getHttpParams(urlParams);
        return this.http.post(this.url(method), body, { params: requestParams });
    }
    /**
     * Do a PUT request to the specified 2sxc api controller
     */
    put(method, urlParams, body) {
        const requestParams = getHttpParams(urlParams);
        return this.http.put(this.url(method), body, { params: requestParams });
    }
    /**
     * Do a DELETE request to the specified 2sxc api controller
     */
    delete(method, urlParams) {
        const requestParams = getHttpParams(urlParams);
        return this.http.put(this.url(method), { params: requestParams });
    }
}

